import React from 'react';
import PropTypes from 'prop-types';
import Layout from '../components/layout';
import Seo from '../components/seo';
import { Link, graphql } from 'gatsby';
import { slugify_util } from '../utils/slugify';

const TagsPage = ({
  location,
  data: {
    allMarkdownRemark: { group },
    site: {
      siteMetadata: { title },
    },
  },
}) => {
  const siteTitle = title || `Title`;
  return (
    <Layout location={location} title={siteTitle}>
      <Seo title="All posts" />

      <h1 className="text-3xl lg:text-5xl md:text-5xl mb-8 font-bold tracking-tighter">All Tags</h1>

      <div className="flex flex-wrap gap-2">
        {group.map((tag) => (
          <Link key={tag.fieldValue} className="p-0 m-0 no-underline" to={`/blog/tag/${slugify_util(tag.fieldValue)}`}>
            <span
              className="flex
                    items-center bg-neutral-200 dark:bg-neutral-800 rounded-md
                    text-xs uppercase p-1 pl-2 pr-2 border-solid border 
                    border-slate-300 hover:border-orange-300
                    dark:border-slate-700 dark:hover:border-orange-400"
            >
              {tag.fieldValue} ({tag.totalCount})
            </span>
          </Link>
        ))}
      </div>
    </Layout>
  );
};

TagsPage.propTypes = {
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      group: PropTypes.arrayOf(
        PropTypes.shape({
          fieldValue: PropTypes.string.isRequired,
          totalCount: PropTypes.number.isRequired,
        }).isRequired,
      ),
    }),
    site: PropTypes.shape({
      siteMetadata: PropTypes.shape({
        title: PropTypes.string.isRequired,
      }),
    }),
  }),
};

export default TagsPage;

export const pageQuery = graphql`
  query TagsPage {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(filter: { frontmatter: { draft: { ne: true } } }, limit: 2000) {
      group(field: { frontmatter: { tags: SELECT } }) {
        fieldValue
        totalCount
      }
    }
  }
`;
